body{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background: #5372f0;
    
}

.wrapper{
    width: 605px;
    background: #fff;
    border: 1px solid black;
    border-radius: 15px;
    padding: 30px 30px 25px;

}

.wrapper header{
    font-size: 35px;
    font-weight: 800;
    text-align: center;
}
.wrapper p{
    font-size: 30px;
    font-weight: 200;
}

.wrapper .btn{
    border-top: 1px solid #ccc;
    border: 1px solid black;
    border-radius: 20px;
}